import React, { useContext } from "react";
import AllHeader from "../components/AllHeader";
import Footer from "../components/Footer";
import { Link } from "react-router";
import { apiData } from "../Data/AllData";
import { CartProvider, useCart } from "react-use-cart";

function Page() {
  const data = useContext(apiData);
  const { addItem } = useCart();
  return (
    <div>
      <div className="section properties">
        <div className="container">
          <div className="row properties-box">
            {data.map((item) => {
              return (
                <div className="col-lg-4 col-md-6 align-self-center mb-30 properties-items col-md-6 adv">
                  <div className="item">
                    <Link to="property-details.html">
                      <img src={item.images[0]} alt="" />
                    </Link>
                    <span className="category">{item.title}</span>
                    <h6>${item.price}</h6>
                    <h4>
                      <Link to="property-details.html">{item.category}</Link>
                    </h4>
                    <ul>
                      <li>
                        rating: <span>{item.rating}</span>
                      </li>
                      <li>
                        stock: <span>{item.stock}</span>
                      </li>
                      <li>
                        brand: <span>{item.brand}</span>
                      </li>
                    </ul>
                    <div className="main-button">
                      <button onClick={() => addItem(item)}>Add to cart</button>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
}

function AllProducts() {
  return (
    <CartProvider>
      <AllHeader />
      <Page />
      <Footer />
    </CartProvider>
  );
}

export default AllProducts;
