import React from "react";
import AllHeader from "../components/AllHeader";
import Footer from "../components/Footer";
import "./cart.css";
import { useCart, CartProvider } from "react-use-cart";
import { Link } from "react-router";
import { isEmpty } from "lodash";
function Cart() {
  const { items, cartTotal, totalUniqueItems, updateItemQuantity, removeItem } =
    useCart();
  if (isEmpty(items)) {
    return (
      <div>
        <h4 className="text-center">Your Cart Still Empty...</h4>
      </div>
    );
  } else {
    return (
      <div>
        <main class="page">
          <section class="shopping-cart dark">
            <div class="container">
              <div class="block-heading">
                <h2>Shopping Cart ({totalUniqueItems})</h2>
              </div>
              <div class="content">
                <div class="row">
                  <div class="col-md-12 col-lg-8">
                    <div class="items">
                      {items.map((item) => {
                        return (
                          <div class="product">
                            <div class="row">
                              <div class="col-md-3">
                                <img
                                  class="img-fluid mx-auto d-block image"
                                  src={item.images[0]}
                                  alt=""
                                />
                              </div>
                              <div class="col-md-8">
                                <div class="info">
                                  <div class="row">
                                    <div class="col-md-5 product-name">
                                      <div class="product-name">
                                        <Link to="#">{item.title}</Link>
                                        <div class="product-info">
                                          <div>
                                            Rating:
                                            <span class="value">
                                              {item.rating}
                                            </span>
                                          </div>
                                          <div>
                                            Category:{" "}
                                            <span class="value">
                                              {item.category}
                                            </span>
                                          </div>
                                          <div></div>
                                        </div>
                                      </div>
                                    </div>
                                    <div class="col-md-4 quantity">
                                      <label for="quantity">Quantity:</label>
                                      <p>{item.quantity}</p>
                                      <button
                                        className="btn btn-warning"
                                        onClick={() =>
                                          updateItemQuantity(
                                            item.id,
                                            item.quantity + 1
                                          )
                                        }
                                      >
                                        {" "}
                                        +{" "}
                                      </button>
                                      <button
                                        className="btn btn-success mx-2"
                                        onClick={() =>
                                          updateItemQuantity(
                                            item.id,
                                            item.quantity - 1
                                          )
                                        }
                                      >
                                        {" "}
                                        -{" "}
                                      </button>
                                      <button
                                        className="btn btn-danger"
                                        onClick={() => removeItem(item.id)}
                                      >
                                        &times;
                                      </button>
                                    </div>
                                    <div class="col-md-3 price">
                                      <span>${item.quantity * item.price}</span>
                                    </div>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>
                        );
                      })}
                    </div>
                  </div>
                  <div class="col-md-12 col-lg-4">
                    <div class="summary">
                      <h3>Summary</h3>
                      <div class="summary-item">
                        <span class="text">Subtotal</span>
                        <span class="price">${cartTotal}</span>
                      </div>
                      <div class="summary-item">
                        <span class="text">Discount</span>
                        <span class="price">$20</span>
                      </div>
                      <div class="summary-item">
                        <span class="text">Shipping</span>
                        <span class="price">$50</span>
                      </div>
                      <div class="summary-item">
                        <span class="text">Total</span>
                        <span class="price">${cartTotal - 20 + 50}</span>
                      </div>
                      <button
                        type="button"
                        class="btn btn-primary btn-lg btn-block"
                      >
                        Checkout
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </section>
        </main>
      </div>
    );
  }
}

function AllCart() {
  return (
    <CartProvider>
      <AllHeader />
      <Cart />
      <Footer />
    </CartProvider>
  );
}

export default AllCart;
